body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{background-color:rgba(0,0,0,0.7);background-image:url(/Tomatillos/static/media/Delphi-Lux-cinema-1-1024x682.37584182.jpg);background-size:100%;background-blend-mode:darken;margin:0;width:100vw;height:100vh;overflow:hidden}.App a{cursor:pointer;color:#ffffff;font-size:18px;text-decoration:none;margin:0 10px;padding-bottom:5px;margin-left:40px;margin-top:20px}.App a:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}h2.error{color:#fff;margin:auto;background-color:rgba(49,59,58,0.7);-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px);border:1px solid #933838;padding:40px}

.movies-container{display:flex;flex-wrap:wrap;height:80vh;overflow:scroll;margin:20px;padding:0 20px}

.small-movie-card{box-shadow:0 0 24px 0 rgba(0,0,0,0.5);margin:0 20px 40px}.small-movie-card header{position:relative}.small-movie-card header img{cursor:pointer;width:30px;position:absolute;top:-5px;right:-5px}.small-movie-card header img:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.small-movie-card header img:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.small-movie-card p{color:#fff;font-size:18px;font-weight:700;text-align:center}.small-movie-card p.rate-number{background-color:#933838;border-radius:50%;margin:-10px auto;padding:0.4em 3.5em;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;position:relative;z-index:3}.small-movie-card .title-block{background:rgba(49,59,58,0.8);-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px);display:flex;align-items:center;justify-content:center;margin:-10px auto 0;padding:15px 10px;position:relative;width:210px;height:70px}.small-movie-card .title-block>p{color:#e64c3c;cursor:pointer;font-family:"Ubuntu",sans-serif;font-size:24px}

.loading{display:flex;align-items:center;margin:auto;position:relative}.loading .light{background:#fffa00;background:-webkit-gradient(linear, left top, right top, from(rgba(247,241,77,0)), color-stop(27%, #f7f14d), to(rgba(255,250,0,0)));background:linear-gradient(90deg, rgba(247,241,77,0) 0%, #f7f14d 27%, rgba(255,250,0,0) 100%);width:300px;height:150px;-webkit-clip-path:polygon(0 40%, 100% 0%, 100% 100%, 0 60%);clip-path:polygon(0 40%, 100% 0%, 100% 100%, 0 60%);-webkit-animation:flash 5s linear infinite;animation:flash 5s linear infinite}.loading>img{width:120px;height:120px;margin-top:-55px}.loading .movie-circle{width:60px;position:absolute;top:18px;left:38px;-webkit-animation:rotate 5s linear infinite;animation:rotate 5s linear infinite}@-webkit-keyframes flash{0%{opacity:1}25%{opacity:0}50%{opacity:1}75%{opacity:0}100%{opacity:1}}@keyframes flash{0%{opacity:1}25%{opacity:0}50%{opacity:1}75%{opacity:0}100%{opacity:1}}@-webkit-keyframes rotate{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes rotate{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.header{align-items:center;background-color:rgba(49,59,58,0.7);border-bottom:1px solid #933838;display:flex;height:10vh;justify-content:space-between;margin-bottom:20px}h1{color:#e64c3c;text-shadow:0px 0px 24px rgba(230,76,60,0.5);font-family:"Ubuntu",sans-serif;font-size:3rem;margin-left:1.5%}.toggleLogin{background-color:#933838;border:none;border-radius:45px;color:#fff;cursor:pointer;font-family:"Gothic A1",sans-serif;font-size:1rem;font-weight:700;height:40px;outline:none;text-decoration:none;width:100%}.toggleLogin:active{background-color:black;color:#fff}#login-link{margin-right:1.5%;width:10%}#logout{margin-right:1.5%;width:10%}

.ratings{display:flex;flex-wrap:wrap;height:80vh;overflow:scroll;margin:20px 20px;padding:40px 20px 20px;position:relative}.ratings a{color:#fff;text-decoration:none;cursor:pointer;font-size:18px;background:none;border:none;position:absolute;top:0;left:0;margin:0;margin-left:20px}.ratings a:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}

.single-movie-info h2{margin-top:100px;margin-left:350px;display:inline-block;padding:20px;border:#933838 solid 3px;background:rgba(49,59,58,0.7);color:#fff;text-shadow:0 0 24px #000}.single-movie-info>button{margin-left:20px;position:absolute}.single-movie-info button{color:#fff;cursor:pointer;font-size:18px;background:none;border:none}.single-movie-info button:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.img-container{position:absolute;left:100px;top:160px;z-index:1;box-shadow:0 0 48px 0 #000}.overview-box{border:#933838 solid 3px;background:rgba(49,59,58,0.7);-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px);width:55%;margin:auto;padding:20px 20px 20px 90px}.overview-box h3{margin:0}.overview-box h3:first-child{margin-bottom:5px}.overview-box>p{line-height:1.5em;min-height:100px;max-height:300px;overflow-x:scroll}.overview-box h3,.overview-box p{color:#fff}.overview-box footer{text-align:center;width:100%;margin-top:80px}.overview-box footer button{margin:auto;background-color:#933838;border-radius:20px;padding:0.4em 2em}

.rating-box h3{font-weight:400;padding:5px 20px;border-bottom:1px solid #933838;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:auto}.rating-box div{display:flex;justify-content:center;align-items:center}.rating-box div *{cursor:pointer;margin:10px 5px}.rating-box div *:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.rating-box div *:active{-webkit-transform:scale(0.8);transform:scale(0.8)}.rating-box div p{padding:0;font-size:24px}.rating-box div p:hover{color:#e64c3c;text-shadow:0px 0px 24px rgba(230,76,60,0.5)}.rating-box div img{width:16px;margin-left:10px}

.login-form{-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px);background-color:rgba(49,59,58,0.7);border:2px solid #933838;color:#fff;font-family:"Gothic A1",sans-serif;height:45%;margin-left:auto;margin-right:auto;margin-top:15%;text-align:center;width:35%}.login-form h3{font-family:"Ubuntu",sans-serif;font-size:2rem}.input-wrapper{display:flex;flex-direction:column;margin-left:auto;margin-right:auto;text-align:left;width:50%}.input-wrapper input{font-size:1.3rem;height:30px;margin-bottom:20px;border:none}.input-wrapper label{font-size:0.8rem;margin-bottom:5px;text-transform:uppercase}.sign-in{background-color:#933838;border:none;border-radius:20px;color:inherit;cursor:pointer;font-size:1rem;font-weight:bold;margin-bottom:10px;margin-left:auto;margin-right:auto;outline:none;padding:0.5em 2em}.sign-in:hover{background-color:#b84646}.sign-in:active{background-color:black}#back-home{color:#fff;font-size:0.8rem;margin-left:auto;margin-right:auto}.error{color:red;font-size:0.8rem;margin-bottom:10px;margin-left:auto;margin-right:auto}#sign-in-link{color:white;height:100%;text-decoration:none;width:100%}

